// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
//import router from './router'
import axios from 'axios'
import VueRouter from 'vue-router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import home from './components/home'
import mypage from './components/mypage'
import subject from './components/subject'
import routedesign from './components/routedesign'
import activity from './components/activity'
import textinformation from './components/textinformation'
import recommand from './components/recommand'
import celebrity from './components/celebrity'
import listening from './components/listening'
import store from './components/store'
import luxun from './components/luxun'
import specialroutes from './components/specialroutes'
import baiyangdian from './components/baiyangdian'
import article from './components/article'
import artifact from './components/artifact'
import login from './components/login'
import echarts from 'echarts'
import audio from './assets/music/shenxian.mp3'
import audioo from './assets/music/kakaka.mp3'
// 方法1：注册播放音频事件到Vue实例上
Vue.prototype.playAudio = () => {
  let buttonAudio = document.getElementById('eventAudio');
  buttonAudio.setAttribute('src',audio)
  buttonAudio.play()
}

Vue.prototype.playAudioo = () => {
  let buttonAudio = document.getElementById('eventAudio');
  buttonAudio.setAttribute('src',audioo)
  buttonAudio.play()
}





Vue.prototype.$axios=axios


Vue.use(VueRouter);
Vue.use(ElementUI);
Vue.config.productionTip = false
let router= new VueRouter({
  routes:[
    {path:'/',component:home},
    {path:'/mypage',component:mypage},
    {path:'/luxun',component:luxun},
    {path:'/article/:id',component:article},
    {path:'/subject',component:subject},
    {path:'/routedesign',component:routedesign},
    {path:'/activity',component:activity},
    {path:'/specialroutes',component:specialroutes},
    {path:'/baiyangdian',component:baiyangdian},
    {path:'/textinformation',component:textinformation},
    {path:'/recommand',component:recommand},
    {path:'/listening',component:listening},
    {path:'/celebrity',component:celebrity},
    {path:'/store',component:store},
    {path:'/artifact',component:artifact},
    {path:'/login',component:login}
]
})

Vue.prototype.$echarts=echarts
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,

  render: c => c(App),
})



