<template>
    <div class="activity">
<div class="pageHeader">
  <el-page-header @back="goBack" content="近日活动" >
</el-page-header>
  </div>
       <div class="input"> 
  <el-input v-model="input" placeholder="活动内容"></el-input>
  </div>
   <div class="searchtext" >
  <el-button type="info" size="mini">搜索</el-button> 
   </div>
   <el-form>
   <ul class="news-list">
       <li 
       v-for="(item, index) in list"
       :key="index"
       class="news-item" @click="toNews(item)">
       <div class="news-media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/activity/first.png">
           <div class="news-profile">
               <a class="title" href="">{{item.title}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.source}}</span>
               
               </p></div>
       </div>
       <div class="media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/activity/deng.png">
           <div class="news-profile">
               <a class="title" href="">{{item.ktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.ksource}}</span>

               </p></div>
       </div>
       <div class="kmedia">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/activity/third.png">
           <div class="news-profile">
               <a class="title" href="">{{item.kktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.kksource}}</span>
               </p></div>
       </div>
       </li></ul>
</el-form>
<div class="block">
  <el-pagination
    layout="prev, pager, next"
    :total="50">
  </el-pagination>
</div>
        </div>
</template>
<script>
export default {
    methods:{
       goBack(){
  this.$router.go(-1);
       }},
       data(){
           return{
               list:[{
                   "id":0,
                   "title":"9月29日：夜间传统文化晚会",
                   "source":"参与者需要提前报名，提供姓名、手机、地址信息。该晚会有民族艺术团表演杂技、传统舞蹈、民族歌曲演奏、传统文化主题小品，演出采用全新艺术样式，以现场演奏原创大型交响乐《钱塘江》、《大运河》为主体，配以大型合唱、朗诵、舞蹈，著名艺术家表演等，展示浙江丰厚的人文底蕴和昂扬的传统文化精神。参与者可以上台与演员、歌手进行互动，最后获得合照机会。",
                    "category":"文字",
                   "time":"2018-09-07",

                   "ktitle":"9月30日：“龙腾喜庆丰收年”活动",
                   "ksource":"活动以舞稻穗龙为主。稻穗龙舞蹈风格独特。该舞蹈源于生活，生动逼真，金黄的稻穗、鲜艳的服饰、群龙欢腾的场景，都给人以美的享受。通过小龙嬉闹、荷花盛开、稻穗欢腾、稻穗变龙、群龙戏珠、群龙翻腾嬉戏、飞龙冲天这样一个舞蹈过程，表达了传统人民欢庆丰收和对美好生活的憧憬和追求。舞蹈由男女合演，龙分一条大龙和20条小龙，小龙由女性表演，大龙由12名男性表演。舞龙表演主要包括两部分，化龙前和化龙后。 化龙前的演出，以摆阵势，营造喜庆、热闹的氛围为主，主要有小龙嬉闹、荷花盛开、稻穗欢腾、稻穗变龙、群龙戏珠等几个阵势。",
                   
                   "kktitle":"10月8日：庆丰收灯会",
                   "kksource":"从傍晚时分就有十数条游船停在崔庄子村中亭河段上准备放河灯。大红色的对联醒目地立在主船上：古为今用庆丰收，胜水荷香飘四方；横批是：欢庆丰收。整个河灯节上大约要放三千盏河灯，天空中还有焰火表演。此外，还有十档的花会走街和落地表演。晚上七点左右，村民就开始放河灯了，十几条游船伴在两条主船左右，主船上搭有牌坊，悬挂着霓虹灯，一条主船上还有古老的乐社演奏音乐。霓虹灯在河上影影绰绰的煞是好看，动听的乐声从河里飘向中亭河的两岸。",
                 
             
             }]
              
              
           }
       }
}
</script>
<style scoped>
.pageHeader{
  background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
.input{
    max-width: 240px;
    position:relative;
    top:22px;
    left:450px;
  }
   .searchtext{
    position:relative;
    left:705px;
    top:-10px;
  }
  .news-media{
          box-sizing: border-box;
  }
  .title{
      font-size: 22px;
  }
  .news-profile{
      position:relative;
      left:220px;
      top:-170px;
  }
  .media{
      position:relative;
      top:-70px;
  }
   .kmedia{
      position:relative;
      top:-140px;
  }
  .block{
      position:relative;
      top:-170px;
      left:460px;
  }
  .news-mark{
    margin-right: 240px;
  }
  
</style>


