<template>
  <div class="specialroutes">
<div class="pageHeader">
  <el-page-header @back="goBack" content="特色文化旅游路线" >
</el-page-header>
  </div>  
 <el-tabs type="border-card">
  <el-tab-pane label="名人传记相关线路">
     <a class="" href="">{{"全部"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"文学名家"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"政治领袖"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"艺术匠人"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"民间名人"}} </a>
  </el-tab-pane>
  <el-tab-pane label="传说故事相关线路">
    <a class="" href="">{{"全部"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"农耕文化"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"客家传说"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"水寨故事"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"普洱乡土故事"}} </a>

  </el-tab-pane>
  <el-tab-pane label="传统技艺相关线路">
    <a class="" href="">{{"全部"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"中国功夫"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"针灸按摩"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"刺绣"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"剪纸"}} </a></el-tab-pane>
  <el-tab-pane label="古建遗存相关线路">
     <a class="" href="">{{"全部"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"秦汉三国时期"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"南北朝时期"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"隋唐五代时期"}} </a>&nbsp;&nbsp;&nbsp;
    <a class="" href="">{{"西夏时期"}} </a>
  </el-tab-pane>
   <el-tab-pane label="古树古木相关线路">古树古木相关线路</el-tab-pane>
</el-tabs>
   <el-row :gutter="20">
  <el-col :span="17"><div class="grid-content bg-purple"></div>
   <el-form>
   <ul class="news-list">
       <li 
       v-for="(item, index) in list"
       :key="index"
       class="news-item" @click="toNews(item)">
       <div class="news-media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/routes/first.png">
           <div class="news-profile">
               <a class="title" >{{item.title}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.source}}</span>
               
               </p>
               <el-rate
  v-model="value1"
  disabled
  show-score
  text-color="#ff9900"
  score-template="{value}">
</el-rate>
</div>
       </div>
       <div class="media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/routes/second.png">
           <div class="news-profile">
               <a class="title" >{{item.ktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.ksource}}</span>

               </p>
               <el-rate
  v-model="value2"
  disabled
  show-score
  text-color="#ff9900"
  score-template="{value}">
</el-rate>
               </div>
                
       </div>
       <div class="kmedia">
           <img style="width: 210px; height: 210px"  src="../assets/routes/third.png" 
           @click="goBaiyangdian()" alt class>
           <div class="news-profile">
               <a class="title" onclick="goBaiyangdian()">{{item.kktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.kksource}}</span>
               </p>
                <el-rate
  v-model="value3"
  disabled
  show-score
  text-color="#ff9900"
  score-template="{value}">
</el-rate>
               </div>
       </div>
       </li></ul>
</el-form>
 </el-col>
  <el-col :span="7"><div class="grid-content bg-purple"></div>

  <div class="design">
      <el-card class="box-card">
  <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="文化主题  ">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
   <el-form-item label="旅行时间">
    <el-col :span="11">
      <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 160%;"></el-date-picker>
    </el-col>
    
  </el-form-item>
  <el-form-item label="选择人数">
  <el-input-number size="mini" v-model="num1" @change="handleChange" :min="1" :max="10" label="选择人数"></el-input-number>
  </el-form-item>
  <el-form-item>
    <el-button type="success" size="mini" @click="onSubmit">查看定制文化路线</el-button>
    
  </el-form-item>
  </el-form>
  </el-card>
  </div>
  </el-col>
 
</el-row>

<div class="block">
  <el-pagination
    layout="prev, pager, next"
    :total="50">
  </el-pagination>
</div>
        </div>
</template>
<script>
export default {
    methods:{
       goBack(){
  this.$router.go(-1);
       },
       goBaiyangdian(){
           var _this = this;
       _this.$router.push('/baiyangdian'); 
        }},
       data(){
           return{
             num1: 1,
               form:{
                   name:'',
                   date1:''
                    },
                    value1: 3.7,
                    value2: 3.3,
                    value3: 3.9,
               list:[{
                   "id":0,
                   "title":"<昆明-腾冲-芒市-瑞丽-和顺古镇游>",
                   "source":"和顺侨乡：古法造纸、木雕、扯丝糖、土锅酒、打铁、织布、洞经、皮影戏、和顺老物件……它们无一不在证明：不伪装的古朴更能历久弥香。全景腾冲：“极边一城”古代川滇缅印南方陆上“丝绸之路”必经之地，“马背上驼来的玉石之城”恬淡幽静的田园风光",
                    "category":"文字",
                   "time":"2018-09-07",

                   "ktitle":"<长沙-韶山-张家界-凤凰古城-庐山-景德镇-井冈山-婺源游>",
                   "ksource":"庐山、油菜花圣地—篁岭、《舌尖上的中国3》拍摄地—源头、瓷都景德镇、红色革命圣地井冈山;享江西特色菜，特别安排婺源徽菜、井冈山红米饭、主席婚宴--品地方风味",
                   
                   "kktitle":"<白洋淀双汽游>",
                   "kksource":"荷花大观园，孙犁纪念馆了解孙犁老先生革命文人的高风亮节和辉煌的文学成就；保定文学馆了解燕赵大地物华天宝、人杰地灵的历史文化及小兵张嘎的英雄事迹；白洋淀抗日战争纪念馆了解白洋淀抗战历史，再现雁翎队痛击鬼子、端岗楼的英雄事迹，园中荷花淀就是当年雁翎队战斗过的地方；踏七十二连桥观赏湿地旖旎风光，情趣桥惊险刺激；",
                 
             
             }]
              
              
           }
       }
}
</script>
<style scoped>
.pageHeader{
  background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
.input{
    max-width: 240px;
    position:relative;
    top:22px;
    left:450px;
  }
  .design{
      position:relative;
      top:60px;
  }
   .searchtext{
    position:relative;
    left:705px;
    top:-10px;
  }
  .news-media{
          box-sizing: border-box;
  }
  .title{
      font-size: 22px;
      font-weight: bold;
  }
  .news-profile{
      position:relative;
      left:220px;
      top:-200px;
  }
  .media{
      position:relative;
      top:-70px;
  }
   .kmedia{
      position:relative;
      top:-140px;
  }
  .block{
      position:relative;
      top:-170px;
      left:460px;
  }
  .news-mark{
    margin-right: 240px;
  }
  
</style>


