<template>
  <div class="article">
      
  <div class="pageHeader">
  <el-page-header @back="goBack" content="游记分享" >
</el-page-header>
  </div>
  <div v-html="article"></div>
  </div>    
</template>

<script>
export default {
    methods:{
        goBack(){
  this.$router.go(-1);
       },
      },
   data(){
       return{
         article:''
       }
   },
   created(){
     console.log(this.$route.params.id);
     this.$axios.get('http://localhost/note/getNoteByName/'+this.$route.params.id)
     .then(res=>{
         console.log(res.data);
         this.article=res.data;
     })
     .catch(err=>{
         console.log('error');
     })
   },
}
</script>

<style>
.pageHeader{
  background-color:#ffc928;
  height:50px; 
}
</style>


