<template>
  <div class="home">
  
<div class="line"></div>
<el-menu
  :default-active="activeIndex2"
  class="el-menu-demo"
  mode="horizontal"
  @select="handleSelect"
  background-color="#333b41"
  text-color="#ffc928"
  active-text-color="#46A3FF">
  <el-menu-item index="1">首页</el-menu-item>
  <el-submenu index="2">
    <template slot="title">文化资料</template>
    <el-menu-item index="2-1" @click.native="goTextinformation()">文字资料</el-menu-item>
   
    <el-menu-item index="2-3" @click.native="goListening()">语音资料</el-menu-item>
   
  </el-submenu> 
   <el-menu-item index="4" divided @click.native="goSpecialroute()">热门文化旅行路线</el-menu-item>
  <el-menu-item index="5">文化攻略</el-menu-item>
    <el-menu-item index="6" divided @click.native="goActivity()">近日活动</el-menu-item>
    <el-menu-item index="7" divided @click.native="goSubject()">游记分享</el-menu-item>
    <el-menu-item index="8" divided @click.native="goRecommand()">大咖推荐</el-menu-item>
    <el-menu-item index="5" divided @click.native="goRoutedesign()">个人路线定制</el-menu-item>
  <el-menu-item index="3" divided @click.native="goStore()">特色商城</el-menu-item>
  <el-menu-item index="9" divided @click.native="goMypage()">我的业务管理</el-menu-item>
  <!--<el-menu-item index="4"><a href="https://www.ele.me" target="_blank">我的业务</a></el-menu-item>-->
<el-menu-item index="10">
<div class="login">
<el-header style="text-align: right; font-size: 12px">
      <el-dropdown>
        <i class="el-icon-setting" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>账号设置</el-dropdown-item>
          <el-dropdown-item>消息通知</el-dropdown-item>
          <el-dropdown-item divided @click.native="goLogin()">退出登陆</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <span>wyx</span>
    </el-header>
</div>
</el-menu-item>

</el-menu>



    <el-carousel :interval="5000" arrow="always" height="350px">
    <el-carousel-item v-for="(img,index) in imgList" :key="index">
      <img v-bind:src="img.url">
    </el-carousel-item>
  </el-carousel>
  <div class="input"> 
  <el-input v-model="input" placeholder="感兴趣的乡土文化类别"></el-input>
  </div>
   <div class="searchtext" >
  <el-button type="info">搜索</el-button> 
   </div>
  <div class="item">
 <img style="width: 200px; height: 200px" 
    src="../assets/classify/artifact.jpg" 
     @click="goArtifact()" alt class>
  <img style="width: 200px; height: 200px" 
    src="../assets/classify/celebrity.jpg" 
    @click="goCelebrity()" alt class>
   <img style="width: 200px; height: 200px" 
    src="../assets/classify/family.jpg" 
     @click="goFamily()" alt class>
   <img style="width: 200px; height: 200px" 
    src="../assets/classify/legend.jpg" 
    @click="goLegend()" alt class>
   <img style="width: 200px; height: 200px" 
    src="../assets/classify/relic.jpg" 
    @click="goRelic()" alt class>
   <img style="width: 200px; height: 200px" 
    src="../assets/classify/tree.jpg" 
    @click="goTree()" alt class>
    </div>
    <div class="slide">
    <el-carousel :interval="4000" type="card" height="200px">
    <el-carousel-item v-for="(img,index) in item" :key="index">
      <img v-bind:src="img.url">
    </el-carousel-item>
  </el-carousel>
    </div>
    <div class="p" >
  <p>大咖推荐</p> 
   </div>
    <div class="item">
 <img style="width: 240px; height: 240px" 
    src="../assets/po/first.png" 
     @click="goArtifact()" alt class>
  <img style="width: 240px; height: 240px" 
    src="../assets/po/second.png" 
    @click="goCelebrity()" alt class>
 
 <img style="width: 240px; height: 240px" 
    src="../assets/po/forth.png" 
    @click="goLegend()" alt class>
 <img style="width: 240px; height: 240px" 
    src="../assets/po/fifth.png" 
    @click="goRelic()" alt class>
   <img style="width: 240px; height: 240px" 
    src="../assets/po/sixth.png" 
    @click="goRelic()" alt class>
  
    </div>
  </div>
  
</template>

<script>

export default {
    mounted(){
this.$axios.get('http://localhost/note/getNoteList')
  .then((res)=>{
    console.log(res.data);
    this.noteList=res.data;
    console.log(this.noteList);
  })
  .catch((error)=>{
    console.log('接口调用不成功');
  })
    },
    data() {
      return {
        noteList:[],
        activeIndex: '1',
        activeIndex2: '1',
        imgList:[
          {url:require('../assets/topslide/second.png')},
          {url:require('../assets/topslide/first.png')},
          {url:require('../assets/topslide/third.png')},
          {url:require('../assets/topslide/forth.png')}
        ],
       item:[
          {url:require('../assets/bottomslide/second.png')},
          {url:require('../assets/bottomslide/first.png')},
          {url:require('../assets/bottomslide/third.png')}
   
        ]
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
       goLogin(){
          var _this = this;
       _this.$router.push('/');
        },
       goListening(){
          var _this = this;
       _this.$router.push('/listening');
        },
        goSubject(){
          var _this = this;
       _this.$router.push('/luxun');
        },
      goMypage(){
          var _this = this;
       _this.$router.push('/mypage');
        },
        goRoutedesign(){
          var _this = this;
       _this.$router.push('/routedesign');
        },
        goCelebrity(){
           var _this = this;
       _this.$router.push('/celebrity');
        },
        goActivity(){
          var _this = this;
       _this.$router.push('/activity');
        },
        goSubject(){
          var _this = this;
       _this.$router.push('/subject');
        },
        goTextinformation(){
          var _this = this;
       _this.$router.push('/textinformation'); 
        },
        goRecommand(){
           var _this = this;
       _this.$router.push('/recommand'); 
        },
         goStore(){
           var _this = this;
       _this.$router.push('/store'); 
        },
         goSpecialroute(){
           var _this = this;
       _this.$router.push('/specialroutes'); 
        },
        goArtifact(){
          var _this = this;
       _this.$router.push('/artifact'); 
        }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
 .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .login{
    color:#ffc928;
    
  }
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .item{
    display: block;
    position:relative;
    top:30px; 
  }
  .slide{
    position:relative;
    top:40px;
  }
  .p{
    font-size: 30px;
    position: relative;
    left:550px;
    top:20px;
  }
  .input{
    
    max-width: 240px;
    position:relative;
    top:28px;
    left:450px;
  }
  .searchtext{
    position:relative;
    left:735px;
    top:-12px;
  }

</style>



