     &nbsp;&nbsp;&nbsp;&nbsp;<br><br><br><br>
     <span>{{"白洋淀位于太行山东麓永定河冲积扇与潴沱河冲积扇相夹持的低洼地区，是华北平原常年积水的较大湖泊。上承大清河南支的潴龙、孝义、唐、府、漕、瀑、萍等大小河流的洪水和沥水，自西向东，由南向北构成扇形河网，汇流一处，形成天然洼地。淀内主要由白洋淀、马棚淀、烧车淀、藻苲淀等大小不等的143个淀泊和3700多条壕沟组成。构成了淀中有淀，沟壕相连，园田和水面相间分布特殊地貌。"}}</span> 
    
     &nbsp;&nbsp;&nbsp;&nbsp;<br><br><br><br><br><br><br><br><br><br><br>
     <span>{{"1927年夏共产党刘亦瑜，受中共保定地委的派遣到淀区北冯村与王家骥等人，建立起淀区第一个党支部。抗日战争时期，活动在白洋淀的抗日武装“雁翎队”，在中国共产党的领导下，利用淀区芦荡遍布，沟河交错的有利地形，开展机动灵活的游击战，以弱胜强，痛击日本侵略军，大长我中华民族之威风，显示出燕赵儿女的聪慧勇敢。作家徐光耀撰写的《小兵张嘎》就取材于“雁翎队”。著名作家孙犁的《荷花淀》，孔厥、袁静的《新儿女英雄传》均以淀区为题材。白洋淀儿女那飒爽英姿，今日仍被全国人民传扬歌颂。"}}</span> 
    
     &nbsp;&nbsp;&nbsp;&nbsp;<br><br><br><br><br><br><br><br><br><br>
     <span>{{"抗日战争时期，在淀泊相连、苇壕纵横的白洋淀上，有一支神出鬼没、来无影去无踪的队伍。他们时而化装成渔民，巧端敌人岗楼；时而出没在敌人运送物资的航线上，截获敌人的军火物资；时而深入敌人的心脏，为民除掉通敌的汉奸；时而头顶荷叶，嘴衔苇管，隐蔽在芦苇丛中，伏击敌人保运船。这支令敌人闻风丧胆、令百姓欢欣鼓舞的队伍，就是活跃在白洋淀上的抗日武装——人称“水上飞将军”的雁翎队。"}}</span> 
