<template>
    <div class="artifact">
<div class="pageHeader">
  <el-page-header @back="goBack" content="传统技艺" >
</el-page-header>
  </div>
       <div class="input"> 
  <el-input v-model="input" placeholder="技艺名称"></el-input>
  </div>
   <div class="searchtext" >
  <el-button type="info" size="mini">搜索</el-button> 
   </div>
   <el-form>
   <ul class="news-list">
       <li 
       v-for="(item, index) in list"
       :key="index"
       class="news-item" @click="toNews(item)">
       <div class="news-media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/artifact/tang.png">
           <div class="news-profile">
               <a class="title" href="">{{item.title}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.source}}</span>
               
               </p></div>
       </div>
       <div class="media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/artifact/zhi.jpg">
           <div class="news-profile">
               <a class="title" href="">{{item.ktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.ksource}}</span>

               </p></div>
       </div>
       <div class="kmedia">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/artifact/tao.jpg">
           <div class="news-profile">
               <a class="title" href="">{{item.kktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.kksource}}</span>
               </p></div>
       </div>
       </li></ul>
</el-form>
<div class="block">
  <el-pagination
    layout="prev, pager, next"
    :total="50">
  </el-pagination>
</div>
        </div>
</template>
<script>
export default {
    methods:{
       goBack(){
  this.$router.go(-1);
       }},
       data(){
           return{
               list:[{
                   "id":0,
                   "title":"非遗糖画",
                   "source":"糖画是一种古老的传统民间手工技艺。糖画，就是以糖做成的画，它亦糖亦画，可观可食。这一颇具民间特色的工艺距今已有400多年的历史。糖画给人们留下了丰富和美好的想象，无论在庙会、集市还是普通的街巷中，它都是最受欢迎的民间技艺之一。2008年，成都糖画入选中国国家第二批非物质文化遗产名录。目前中国的“糖画”已经是享誉世界的非物质文化遗产。糖画是与老百姓生活密切相关、世代相传的民间手工艺之一。糖画制作技艺看似是简单的民间工艺，实则融汇了国画的白描，蕴含了历史、美术、地方民俗风情、蔗糖工艺等复杂的元素，具有较高的民俗艺术价值和文化价值。糖画制作工艺流程复杂，每道工序的细腻程度要求严格，具有很高的工艺价值。",
                    "category":"文字",
                   "time":"",

                   "ktitle":"剪纸艺术",
                   "ksource":"剪纸艺术是最古老的中国民间艺术之一，作为一种镂空艺术，它能给人以视觉上以透空的感觉和艺术享受。剪纸用剪刀将纸剪成各种各样的图案，如窗花、门笺、墙花、顶棚花、灯花等。每逢过节或新婚喜庆，人们便将美丽鲜艳的剪纸贴在家中窗户、墙壁、门和灯笼上，节日的气氛也因此被烘托得更加热烈。在农村，剪纸通常是由妇女、姑娘们来做。在过去，剪纸几乎可以说是每个女孩所必须掌握的手工艺术，并且还被人们来品评新娘的一个标准。在中国南北方的剪纸艺术，通过一把剪刀，一张纸，就可以表达生活中的各种喜怒哀乐。剪纸，作为一种原始艺术的载体，它在造型上总是运用夸张变形的手法，它善于将不同空间、时间的物象进行组合，通过一种夸张和变形的手法来改变对象的性质、形式，进而改变自然原形的惯常标准。",
                   
                   "kktitle":"陶瓷的艺术",
                   "kksource":"陶艺，广泛讲是中国传统古老文化与现代艺术结合的艺术形式。从历史的发展可知，“陶瓷艺术”是一门综合艺术，经历了一个复杂而漫长的文化积淀历程。它与绘画、雕塑、设计、以及其他工艺美术等有着无法割舍的传承与比照关系。二十世纪八十年代中后期，随着西方现代艺术的介入，西方的“当代陶艺”观念对中国陶瓷艺术产生了广泛而深刻的影响，“陶艺”的概念也一度成为了陶瓷艺术界的新时尚。它所体现的仍然是一种东、西方文化的矛盾与碰撞。诸如“何谓陶艺”，“陶艺与陶瓷艺术的关系如何”等一系列看似不是问题的基本的问题，直到如今未能取得统一认识和标准",
                 
             
             }]
              
              
           }
       }
}
</script>
<style scoped>
.pageHeader{
  background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
.input{
    max-width: 240px;
    position:relative;
    top:22px;
    left:450px;
  }
   .searchtext{
    position:relative;
    left:705px;
    top:-10px;
  }
  .news-media{
          box-sizing: border-box;
  }
  .title{
      font-size: 22px;
  }
  .news-profile{
      position:relative;
      left:220px;
      top:-170px;
  }
  .media{
      position:relative;
      top:-70px;
  }
   .kmedia{
      position:relative;
      top:-140px;
  }
  .block{
      position:relative;
      top:-170px;
      left:460px;
  }
  .news-mark{
    margin-right: 240px;
  }
  
</style>


