<template>
    <div class="textinformation">
<div class="pageHeader">
  <el-page-header @back="goBack" content="文字资料" >
</el-page-header>
  </div>
       <div class="input"> 
  <el-input v-model="input" placeholder="乡土文化内容"></el-input>
  </div>
   <div class="searchtext" >
  <el-button type="info" size="mini">搜索</el-button> 
   </div>
   <el-form>
   <ul class="news-list">
       <li 
       v-for="(item, index) in list"
       :key="index"
       class="news-item" @click="toNews(item)">
       <div class="news-media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/textinfo/first.jpg">
           <div class="news-profile">
               <a class="title" href="">{{item.title}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.source}}</span>
               
               </p></div>
       </div>
       <div class="media">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/textinfo/second.png">
           <div class="news-profile">
               <a class="title" href="">{{item.ktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.ksource}}</span>

               </p></div>
       </div>
       <div class="kmedia">
           <img style="width: 210px; height: 210px" class="news-thumbnail" src="../assets/textinfo/third.png">
           <div class="news-profile">
               <a class="title" href="">{{item.kktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.kksource}}</span>
               </p></div>
       </div>
       </li></ul>
</el-form>
<div class="block">
  <el-pagination
    layout="prev, pager, next"
    :total="50">
  </el-pagination>
</div>
        </div>
</template>
<script>
export default {
    methods:{
       goBack(){
  this.$router.go(-1);
       }},
       data(){
           return{
               list:[{
                   "id":0,
                   "title":"乡土文化 根不能断",
                   "source":"乡村文明是中华民族文明史的主体，村庄是这种文明的载体，耕读文明是我们的软实力。乡村文明的传承，影响着文化载体的续存乃至中华民族精神家园的回归与守护。乡村振兴的内涵既包括物质上的富裕，更包括精神上的富足。乡村是历史记忆、文化认同、情感归属的重要载体，蕴藏着丰富的文化资源。",
                    "category":"文字",
                   "time":"2018-09-07",

                   "ktitle":"中国四大雕塑之乡",
                   "ksource":"雕塑是一门古老的艺术，他与人类的艺术创造意识几近同龄，伴随着人类走过近万年的岁月。其中石雕是中国古代雕塑重要的一个分支，现在和大家说说我国的四大石雕之乡：铜雕产生于商周，是以铜料为胚，运用雕刻、铸塑等手法制作的一种雕塑。铜雕艺术主要表现了造型、质感、纹饰的美，多用于表现神秘有威慑力的宗教题材。",
                   
                   "kktitle":"中国人不可丢弃的十大传统手工艺！ ",
                   "kksource":"随着工业时代的到来，机器对手工业带来了巨大的冲击，导致中国很多传统的手工技艺濒临灭绝。然而，传统手工艺除了代表了民间艺人数千年来勤劳和智慧的结晶外，还代表了中华民族文化传承和脉络，是中国作为文明古国最鲜明的国际“名片”。",
                 
             
             }]
              
              
           }
       }
}
</script>
<style scoped>
.pageHeader{
  background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
.input{
    max-width: 240px;
    position:relative;
    top:22px;
    left:450px;
  }
   .searchtext{
    position:relative;
    left:705px;
    top:-10px;
  }
  .news-media{
          box-sizing: border-box;
  }
  .title{
      font-size: 22px;
  }
  .news-profile{
      position:relative;
      left:220px;
      top:-170px;
  }
  .media{
      position:relative;
      top:-70px;
  }
   .kmedia{
      position:relative;
      top:-140px;
  }
  .block{
      position:relative;
      top:-170px;
      left:460px;
  }
  .news-mark{
    margin-right: 240px;
  }
  
</style>


