<template>
<div id="subjectpost">
 <div class="pageHeader">
  <el-page-header @back="goBack" content="我的业务管理" >
</el-page-header>
  </div>
<el-tabs type="border-card">
  <el-tab-pane label="账户基本资料">
    <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="账户名称">
     <el-input v-model="form.name" placeholder="WenYifei"></el-input>
   </el-form-item>
   <el-form-item label="手机号">
     <el-input v-model="form.phonenumber" placeholder="17888886666"></el-input>
   </el-form-item>
   <el-form-item label="电子邮箱">
     <el-input v-model="form.email" placeholder="welcome@sina.com"></el-input>
   </el-form-item>
   <el-form-item label="出生年月">
     <el-input v-model="form.birthday" placeholder="1999/06/16"></el-input>
   </el-form-item>
   <el-form-item label="居住地">
     <el-input v-model="form.place" placeholder="Hangzhou City,Zhejiang Province"></el-input>
   </el-form-item>
   <el-form-item>
     <el-button type="info">修改资料</el-button>
   </el-form-item>
    </el-form>
  </el-tab-pane>
  <el-tab-pane label="我的旅行规划">
      <el-calendar v-model="value">
</el-calendar>

  </el-tab-pane>
  <el-tab-pane label="我的文化资料">
    <ul class="news-list">
       <li 
       v-for="(item, index) in list"
       :key="index"
       class="news-item" @click="toNews(item)">
       <div class="news-media">
           <img style="width: 180px; height: 180px" class="news-thumbnail" src="../assets/textinfo/ancientdesign.png">
           <div class="news-profile">
               <a class="title" href="">{{item.title}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.source}}</span>
                 <br>
                   <span>{{item.ssource}}</span>
                <br>
                   <span>{{item.sssource}}</span>
               </p></div>
       </div>
       <div class="media">
           <img style="width: 180px; height: 180px" class="news-thumbnail" src="../assets/textinfo/ancienttree.png">
           <div class="news-profile">
               <a class="title" href="">{{item.ktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.ksource}}</span>
                 <br>
                   <span>{{item.kssource}}</span>
                <br>
                   <span>{{item.ksssource}}</span>
               </p></div>
       </div>
       <div class="kmedia">
           <img style="width: 180px; height: 180px" class="news-thumbnail" src="../assets/textinfo/third.png">
           <div class="news-profile">
               <a class="title" href="">{{item.kktitle}} </a>
               <p class="news-mark">
                   <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.kksource}}</span>
                 <br>
                   <span>{{item.kkssource}}</span>
                <br>
                   <span>{{item.kksssource}}</span>
               </p></div>
       </div>
       </li></ul>



  </el-tab-pane>
  <el-tab-pane label="我购买的商品">
  <el-row>
  <el-col :span="5" v-for="(o, index) in 3" :key="o" :offset="index > 0 ? 3 : 0">
    <el-card :body-style="{ padding: '0px' }">
      <img src="../assets/speciality/hotpot.jpg" class="image">
      <div style="padding: 14px;">
        <span>重庆火锅底料</span>
        <div class="bottom clearfix">
         <div slot="tip" class="text">价格:{{price}}  日期:{{date}}</div>
          <!--<time class="time">{{ currentDate }}</time>-->
          <el-button type="info" class="button">查看详情</el-button>
        </div>
      </div>
    </el-card>
  </el-col>
</el-row></el-tab-pane>
  <el-tab-pane label="我的旅行日记书">
    <img style="width: 300px; height: 450px" 
    src="../assets/travel/cdc.jpg" 
     @click="goHome()" alt class>
     <img style="width: 300px; height: 450px" 
    src="../assets/travel/jn.jpg" 
     @click="goHome()" alt class>
     <img style="width: 300px; height: 450px" 
    src="../assets/travel/summer.jpg" 
     @click="goHome()" alt class>

<template>
  <div class="buone">
  <el-radio v-model="radio" label="1">选定</el-radio>
<div class="butwo">
  <el-radio v-model="radio" label="2">选定</el-radio></div>
  <div class="buthree">
  <el-radio v-model="radio" label="3">选定</el-radio></div></div>
</template>
<div class="up">
  <span>上传个性相册</span>
<el-upload
  action="https://jsonplaceholder.typicode.com/posts/"
  list-type="picture-card"
  :on-preview="handlePictureCardPreview"
  :on-remove="handleRemove">
  <i class="el-icon-plus"></i>
</el-upload>
<el-dialog :visible.sync="dialogVisible">
  <img width="100%" :src="dialogImageUrl" alt="">
</el-dialog>
</div>
<div class="xin">
<el-row>
<el-button type="info" size="mini">确定</el-button>
</el-row></div>

  </el-tab-pane>
  <el-tab-pane label="我的钱包">
    <span>当前余额:</span>
    <div style="margin: 10px 0 0 50px;"></div>
<el-input
  placeholder="366"
  v-model="input"
  :disabled="true">
</el-input>
  <span>充值：（充值一百元以上享九折优惠！！）</span><br>
 <img style="width: 300px; height: 300px" 
    src="../assets/travel/money.png" 
     @click="goHome()" alt class>
 <img style="width: 300px; height: 294px" 
    src="../assets/travel/moneyy.png" 
     @click="goHome()" alt class>
  <el-button type="info" size="mini">确定</el-button>
  </el-tab-pane>
   <el-tab-pane label="发起活动" >
     <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="活动名称">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="活动区域">
    <el-select v-model="form.region" placeholder="请选择活动区域">
      <el-option label="区域一" value="shanghai"></el-option>
      <el-option label="区域二" value="beijing"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="活动时间">
    <el-col :span="11">
      <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
    </el-col>
    <el-col class="line" :span="2">-</el-col>
    <el-col :span="11">
      <el-time-picker placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
    </el-col>
  </el-form-item>
  <el-form-item label="今日举办">
    <el-switch v-model="form.delivery"></el-switch>
  </el-form-item>
  <el-form-item label="活动性质">
    <el-checkbox-group v-model="form.type">
      <el-checkbox label="乡土文化宣讲活动" name="type"></el-checkbox>
      <el-checkbox label="传统文化沙龙" name="type"></el-checkbox>
      <el-checkbox label="文化遗产讲解" name="type"></el-checkbox>
      <el-checkbox label="文化晚会" name="type"></el-checkbox>
      <el-checkbox label="乡土传统研讨会" name="type"></el-checkbox>
    </el-checkbox-group>
  </el-form-item>
   <el-form-item label="活动人数" >
    <el-input v-model="form.population"></el-input>
  </el-form-item>
  <el-form-item label="特殊资源">
    <el-radio-group v-model="form.resource">
      <el-radio label="线上品牌商赞助"></el-radio>
      <el-radio label="线下场地免费"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="活动内容">
    <el-input type="textarea" v-model="form.desc"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="info" @click="onSubmit">立即创建</el-button>
    <el-button>取消</el-button>
  </el-form-item>
</el-form>
     </el-tab-pane>

  
    <el-tab-pane label="历史旅行数据">
<div class="" id="myChart" :style="{width: '480px', height: '250px'}"></div>

      </el-tab-pane>
   
 
</el-tabs>

  </div>
</template>
  
<script>

 

  export default {
    data() {


      return {
        
        input: '',
         radio: '1',
         dialogImageUrl: '',
        dialogVisible: false,
           value: new Date(),
            list:[{
                   "id":0,
                   "title":"中国传统设计艺术",
                   
                   "source":"中国传统文化博大精深，中华民族的传统装饰艺术也有着悠久和深厚的历史底蕴。现代环艺设计注重传统文化价值",
                   "ssource":"的回归，传统装饰艺术不仅为我们设计提供了丰富的素材，更是取之不尽的宝贵源泉。如何正确运用传统装饰艺术，",
                   "sssource":"使它与时代相得益彰、洋溢生命的气息，是每个设计师值得思考的问题。取其精华，古为今用。",                  
                   "category":"文字",
                   "time":"2018-09-07",

                   "ktitle":"历史悠久的古木文化",
                   "ksource":"木文化是人们生活中与木相关的人文教化、共享的木材价值观和木材利用方式。具体的形式：有形的如具有各地特",
                   "kssource":"色的传统或现代木建筑、木家具、木装璜、木雕、木乐器等。无形的如人们与木、木材及木质环境相关的思想、行",
                   "ksssource":"为和活动（如历史、文学、科教、技术、制度、经济等）。木文化的研究从人文的角度看木材体现人和自然的关系。",
                   
                   "kktitle":"中国人不可丢弃的十大传统手工艺！ ",
                   "kksource":"随着工业时代的到来，机器对手工业带来了巨大的冲击，导致中国很多传统的手工技艺濒临灭绝。然而，传统手工艺除",
                   "kkssource":"了代表了民间艺人数千年来勤劳和智慧的结晶外，还代表了中华民族文化传承和脉络，是中国作为文明古国最鲜明的",
                   "kksssource":"国际“名片”。"
             
             }],
           date:"08/27",
           price:"230",
           form: {
          name: '',
          email:'',
          phonenumber:'',
          birthday:'',
          place:'',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          population:'',
        }
      }
       
    },
    mounted(){
      this.drawLine();
    },
    methods: {
      drawLine(){
let myChart = this.$echarts.init(document.getElementById('myChart'))
myChart.setOption({
tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
    },
    legend: {
        orient: 'vertical',
        left: 10,
        data: ['华东片区', '西南片区', '华北片区', '华中片区', '东北地区']
    },
    series: [
        {
            name: '访问来源',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
                show: false,
                position: 'center'
            },
            emphasis: {
                label: {
                    show: true,
                    fontSize: '30',
                    fontWeight: 'bold'
                }
            },
            labelLine: {
                show: false
            },
            data: [
                {value: 35, name: '华东片区'},
                {value: 30, name: '西南片区'},
                {value: 24, name: '华北片区'},
                {value: 15, name: '华中片区'},
                {value: 88, name: '东北地区'}
            ]
        }
    ]

})



      },
         handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      goHome(){
           var _this = this;
             _this.$router.push('/home');
      },
       goBack(){
  this.$router.go(-1);
       },
      onSubmit() {
        console.log('submit!');
      },
      
    }
  }
</script>

<style>
.xin{
   position:relative;
  left:915px;
  top:-450px;
}
.up{
  position:relative;
  left:915px;
  top:-460px;
}
.buone{
  position:relative;
  left:120px;
  top:2px;
}
.butwo{
  position:relative;
  left:300px;
  top:-23px;
}
.buthree{
  position:relative;
  left:600px;
  top:-44px;
}
 .title{
      font-size: 22px;
  }
  .news-profile{
      position:relative;
      left:220px;
      top:-170px;
  }
  .media{
      position:relative;
      top:-70px;
  }
   .kmedia{
      position:relative;
      top:-140px;
  }
 .pageHeader{
 background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
 .bbutton{
      position:fixed;
     right:520px;
     top:450px;
}
 .is-selected {
    color: #1989FA;
  }
.insect{
    position:fixed;
     right:1000px;
}
.time {
    font-size: 13px;
    color: #999;
  }
  .favor{
 display: block;
    position:relative;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }

</style>


