<template>
<div>
<div class="pageHeader">
  <el-page-header @back="goBack" content="个人路线定制" >
</el-page-header>
  </div>
   <div style="height:100%;">
          <div class="map" id="Map">
          </div>
      </div>
</div>
</template>
<script>
export default {
    name: "bdMap",
      components: {},
      data() {
          return {};
      },
      methods:{
        goBack(){
  this.$router.go(-1);
       }
      },
      mounted() {
          const map = new BMap.Map("Map");
          // 创建地图实例
          const point = new BMap.Point(116.404, 39.915);
          // 创建点坐标
          map.centerAndZoom(point, 15);
          // 初始化地图，设置中心点坐标和地图级别
      } 
    
}
</script>
<style scoped>
.map {
      width: 100%;
      height: 500px;
  }
   .pageHeader{
   background-color:#ffc928;
  height:47px; 
  color:#333b41;

  
}
</style>


