<template>
 <div id="login">
     <div class="pageHeader">
  <el-page-header  content="登陆" >
</el-page-header>
  </div>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="账号" prop="user">
    <el-input type="user" v-model="ruleForm.user" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="密码" prop="password">
    <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
  </el-form-item>
  
  <el-form-item>
    <el-button type="warning" @click="submitForm('ruleForm')">登陆</el-button>
    <el-button type="warning" @click="goRegister()">注册</el-button>
  </el-form-item>
</el-form>
 </div>
</template>

<script>
  export default {
    data() {
    
      var validateUser = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入账号'));
        }
         else {
             if(value='admin'){
                 callback();
             }
          
        }
      };
      var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } 
         else {
             if(value==='123456'){
                  callback();
             }
         
        }
      };
      return {
        ruleForm: {
          user: '',
          password: ''
        
        },
        rules: {
          user: [
            { validator: validateUser, trigger: 'blur' }
          ],
          password: [
            { validator: validatePassword, trigger: 'blur' }
          ]
         
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('登陆成功!');
             var _this = this;
             _this.$router.push('/');
          } 
            else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      goRegister(){
           var _this = this;
             _this.$router.push('/register');
      }
    }
  }
</script>

<style>
.pageHeader{
  background-color:#ffc928;
  height:47px; 
  color:#333b41;
}
</style>


