<template>
<div class="subject">
  
   <div class="pageHeader">
  <el-page-header @back="goBack" content="游记分享" >
</el-page-header>
  </div>
 
  <div class="pic">
  <img style="width: 300px; height: 300px" 
    src="../assets/sub/ware.png" 
    @click="goCelebrity()" alt class>
  </div>

   <div class="lie">
   <ul>
     <li v-for="item in noteList" :key="item">
       <router-link :to="'/article/' + item">{{item}}</router-link>
     </li>
   </ul>
   </div>


</div>
</template>
<script>
 export default {
    methods:{
        goBack(){
  this.$router.go(-1);
       }
      },
   name:"Note",
   mounted(){
this.$axios.get('http://localhost/note/getNoteList')
  .then((res)=>{
    console.log(res.data);
    this.noteList=res.data;
    console.log(this.noteList);
  })
  .catch((error)=>{
    console.log('接口调用不成功');
  })
    },
    data() {
      return {
        noteList:[],
        
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
   },
      goBack(){
  this.$router.go(-1);
       },
    }
  }
</script>
<style scoped>
.pageHeader{
  background-color:#ffc928;
  height:50px; 
}
.pic{
  position:relative;
  top:30px;
  left:140px;
}
.lie{
    color: #ffc928;
    font-size: 26px;
    position:relative;
    left:520px;
    top:-280px;

  }
  .lie:hover{
    color:green;
  }
</style>




